import './Explore.css';
import exploreData from '../../Data/exploreData';
import { Card } from '../../component/index'
import {SectionTitle} from '../../component/index'
const Explore = () => {
    return (
        <>
            <div className='explore mt-24 bg-slate-100 py-5' id="explore">
                <div className='container p-5 mx-auto'>
                     <SectionTitle title="Explore"/>
                    <p className='mx-auto mt-2 text-center mb-5'>Explore New Articls around the world and many more</p>
                    <div className='cards flex flex-row justify-between items-center flex-wrap gap-3 mt-24'>
                        {
                            exploreData.map((data) => {
                                return <Card key={Math.random() * 10} img={data.img} headingSpecial={data.headingSpecial} card_head={data.card_head} classRating={data.classRating} rating={data.rating} rating_num={data.rating_num} rang={data.rang} tour_kind={data.tour_kind} state={data.state} stateClass={data.stateClass} />
                            })
                        }
                    </div>
                </div>
            </div>
        </>
    )
}
export default Explore;